@import url(../vendors/sanitize-10.0.0.css);
@import url(../vendors/forms-10.0.0.css);
@import 'variables.scss';
@import 'mixins/_grid.scss';
@import 'mixins/_helpers.scss';
@import 'types/_fonts.scss';
@import 'types/_headings.scss';
@import 'types/_type.scss';

@import 'global.scss';
@import 'components/_buttons.scss';
@import 'components/_forms.scss';
@import 'components/_lists.scss';
@import 'components/_menus.scss';
@import 'components/_tables.scss';
@import 'components/_tooltips.scss';

@import 'partials/_article.scss';
@import 'partials/_footer.scss';
@import 'partials/_header.scss';

/*
    DJANGO Admin styles
*/

@import url(fonts.css);

body {
    margin: 0;
    padding: 0;
    font-size: 14px;
    font-family: "Roboto","Lucida Grande","DejaVu Sans","Bitstream Vera Sans",Verdana,Arial,sans-serif;
    color: #333;
    background: $baseColor;
}

/* LINKS */

a:link, a:visited {
    color: #447e9b;
    text-decoration: none;
}

a:focus, a:hover {
    color: #036;
}

a:focus {
    text-decoration: underline;
}

a img {
    border: none;
}

a.section:link, a.section:visited {
    color: #fff;
    text-decoration: none;
}

a.section:focus, a.section:hover {
    text-decoration: underline;
}

/* GLOBAL DEFAULTS */

h2.subhead {
    font-weight: normal;
    margin-top: 0;
}


pre.literal-block {
    margin: 10px;
    background: #eee;
    padding: 6px 8px;
}

code strong {
    color: #930;
}

/* TEXT STYLES & MODIFIERS */

.small {
    font-size: 11px;
}

.tiny {
    font-size: 10px;
}

p.tiny {
    margin-top: -2px;
}

.mini {
    font-size: 10px;
}

p.mini {
    margin-top: -3px;
}

.help, p.help, form p.help, div.help, form div.help, div.help li {
    font-size: 11px;
    color: #999;
}

div.help ul {
     margin-bottom: 0;
}

.help-tooltip {
    cursor: help;
}

p img, h1 img, h2 img, h3 img, h4 img, td img {
    vertical-align: middle;
}

.quiet, a.quiet:link, a.quiet:visited {
    color: #999;
    font-weight: normal;
}

.float-right {
    float: right;
}

.float-left {
    float: left;
}

.clear {
    clear: both;
}

.align-left {
    text-align: left;
}

.align-right {
    text-align: right;
}

.example {
    margin: 10px 0;
    padding: 5px 10px;
    background: #efefef;
}

.nowrap {
    white-space: nowrap;
}

/* TABLES */

thead th.required {
    color: #000;
}

tr.alt {
    background: #f6f6f6;
}

.row1 {
    background: #fff;
}

.row2 {
    background: #f9f9f9;
}

/* SORTABLE TABLES */

thead th {
    padding: 5px 10px;
    line-height: normal;
    text-transform: uppercase;
    background: #f6f6f6;
}

thead th a:link, thead th a:visited {
    color: #666;
}

thead th.sorted {
    background: #eee;
}

thead th.sorted .text {
    padding-right: 42px;
}

table thead th .text span {
    padding: 8px 10px;
    display: block;
}

table thead th .text a {
    display: block;
    cursor: pointer;
    padding: 8px 10px;
}

table thead th .text a:focus, table thead th .text a:hover {
    background: #eee;
}

thead th.sorted a.sortremove {
    visibility: hidden;
}

table thead th.sorted:hover a.sortremove {
    visibility: visible;
}

table thead th.sorted .sortoptions {
    display: block;
    padding: 9px 5px 0 5px;
    float: right;
    text-align: right;
}

table thead th.sorted .sortpriority {
    font-size: .8em;
    min-width: 12px;
    text-align: center;
    vertical-align: 3px;
    margin-left: 2px;
    margin-right: 2px;
}

table thead th.sorted .sortoptions a {
    position: relative;
    width: 14px;
    height: 14px;
    display: inline-block;
    background: url(../img/sorting-icons.svg) 0 0 no-repeat;
    background-size: 14px auto;
}

table thead th.sorted .sortoptions a.sortremove {
    background-position: 0 0;
}

table thead th.sorted .sortoptions a.sortremove:after {
    content: '\\';
    position: absolute;
    top: -6px;
    left: 3px;
    font-weight: 200;
    font-size: 18px;
    color: #999;
}

table thead th.sorted .sortoptions a.sortremove:focus:after,
table thead th.sorted .sortoptions a.sortremove:hover:after {
    color: #447e9b;
}

table thead th.sorted .sortoptions a.sortremove:focus,
table thead th.sorted .sortoptions a.sortremove:hover {
    background-position: 0 -14px;
}

table thead th.sorted .sortoptions a.ascending {
    background-position: 0 -28px;
}

table thead th.sorted .sortoptions a.ascending:focus,
table thead th.sorted .sortoptions a.ascending:hover {
    background-position: 0 -42px;
}

table thead th.sorted .sortoptions a.descending {
    top: 1px;
    background-position: 0 -56px;
}

table thead th.sorted .sortoptions a.descending:focus,
table thead th.sorted .sortoptions a.descending:hover {
    background-position: 0 -70px;
}

/* FORM DEFAULTS */

input, textarea, select, .form-row p, form .button {
    font-family: "Roboto", "Lucida Grande", Verdana, Arial, sans-serif;
    font-weight: normal;
    font-size: 13px;
}
.form-row div.help {
    padding: 2px 3px;
}

textarea {
    vertical-align: top;
}

select {
    height: 30px;
}

select[multiple] {
    /* Allow HTML size attribute to override the height in the rule above. */
    height: auto;
    min-height: 150px;
}

/* FORM BUTTONS */

.button, input[type=submit], input[type=button], .submit-row input, a.button {
	float: right;
    background: $accentColor;
    color: $textIconColor;
    cursor: pointer;

	&:active, &:focus, &:hover {
	    background: darken($accentColor, 10);
	}
	
	&[disabled] {
	    opacity: 0.4;
	}
}


.button.default, input[type=submit].default, .submit-row input.default {
    float: right;
    background: $primaryColor;

	&:active, &:focus, &:hover {
	    background: darken($primaryColor, 10);
	}
	&[disabled] {
	    opacity: 0.4;
	}
}

/* MODULES */

.module {
    @include card-style(#FFF, $primaryTextColor);
	border-radius: 5px;
    border: none;
    margin-bottom: 30px;
}

.module p, .module ul, .module h3, .module h4, .module dl, .module pre {
    padding-left: 10px;
    padding-right: 10px;
}

.module blockquote {
    margin-left: 12px;
}

.module ul, .module ol {
    margin-left: 1.5em;
}

.module h3 {
    margin-top: .6em;
}

.module h2, .module caption, .inline-group h2 {
    margin: 0;
    padding: 8px;
    font-weight: 400;
    font-size: 13px;
    text-align: left;
    background: $accentColor;
    color: #fff;
}

.module caption,
.inline-group h2 {
    font-size: 12px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
}

.module table {
    border-collapse: collapse;
}

/* MESSAGES & ERRORS */

ul.messagelist {
    padding: 0;
    margin: 0;
	li {
	    display: block;
	    font-weight: 400;
	    font-size: 13px;
	    padding: 10px 10px 10px 65px;
	    margin: 0 0 10px 0;
	    background: #dfd url(../img/icon-yes.svg) 40px 12px no-repeat;
	    background-size: 16px auto;
	    color: #333;
	
		&.warning {
		    background: #ffc url(../img/icon-alert.svg) 40px 14px no-repeat;
		    background-size: 14px auto;
		}
		
		&.error {
		    background: #ffefef url(../img/icon-no.svg) 40px 12px no-repeat;
		    background-size: 16px auto;
		}
	}
}

.errornote {
    font-size: 14px;
    font-weight: 700;
    display: block;
    padding: 10px 12px;
    margin: 0 0 10px 0;
    color: #ba2121;
    border: 1px solid #ba2121;
    border-radius: 4px;
    background-color: #fff;
    background-position: 5px 12px;
}

ul.errorlist {
    margin: 0 0 4px;
    padding: 0;
    color: #ba2121;
    background: #fff;
	li {
	    font-size: 13px;
	    display: block;
	    margin-bottom: 4px;
	
		&:first-child {
		    margin-top: 0;
		}
		a {
		    color: inherit;
		    text-decoration: underline;
		}
	}
}

td ul.errorlist {
    margin: 0;
    padding: 0;
	li {
	    margin: 0;
	}
}

.form-row.errors {
    margin: 0;
    border: none;
    border-bottom: 1px solid #eee;
    background: none;
}

.form-row.errors ul.errorlist li {
    padding-left: 0;
}

.errors input, .errors select, .errors textarea {
    border: 1px solid #ba2121;
}

div.system-message {
    background: #ffc;
    margin: 10px;
    padding: 6px 8px;
    font-size: .8em;
}

div.system-message p.system-message-title {
    padding: 4px 5px 4px 25px;
    margin: 0;
    color: #c11;
    background: #ffefef url(../img/icon-no.svg) 5px 5px no-repeat;
}

.description {
    font-size: 12px;
    padding: 5px 0 0 12px;
}

/* BREADCRUMBS */

div.breadcrumbs {
    background: lighten($primaryColor, 10);
    padding: 10px 40px;
    border: none;
    font-size: 14px;
    color: #c4dce8;
    text-align: left;
	a {
	    color: #fff;
		&:focus, &:hover {
		    color: #c4dce8;
		}
	}
}

/* ACTION ICONS */

.viewlink:before, .inlineviewlink:before,
.changelink:before, .inlinechangelink:before,
.deletelink:before,
.addlink:before {
	font-family: "entypo";
	margin-right: .5em;
	font-size: 120%;
	text-shadow: 1px 1px 1px #666;
}

.viewlink:before,
.inlineviewlink:before {
	content: '\e820';
	color: rgb(0, 128, 192);
}

.addlink:before {
	content: '\e806';
	color: rgb(0, 255, 64);
}

.changelink:before, .inlinechangelink:before {
	content: '\e83d';
	color: rgb(255, 128, 64);
}

.deletelink {
	margin:5px;
	&:before { content: '\e81f'; }
    //background: url(../img/icon-deletelink.svg) 0 1px no-repeat;
}
a.deletelink:link, a.deletelink:visited {
    color: #CC3434;
}

a.deletelink:focus, a.deletelink:hover {
    color: #993333;
    text-decoration: none;
}

/* OBJECT TOOLS */

.object-tools {
    font-size: 10px;
    font-weight: bold;
    padding-left: 0;
    float: right;
    position: relative;
    margin-top: -48px;
}

.form-row .object-tools {
    margin-top: 5px;
    margin-bottom: 5px;
    float: none;
    height: 2em;
    padding-left: 3.5em;
}

.object-tools li {
    display: block;
    float: left;
    margin-left: 5px;
    height: 16px;
}

.object-tools a {
    border-radius: 15px;
}

.object-tools a:link, .object-tools a:visited {
    display: block;
    float: left;
    padding: 3px 12px;
    background: #999;
    font-weight: 400;
    font-size: 11px;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    color: #fff;
}

.object-tools a:focus, .object-tools a:hover {
    background-color: $primaryColor;
}

.object-tools a:focus{
    text-decoration: none;
}

.object-tools a.viewsitelink, .object-tools a.golink,.object-tools a.addlink {
    background-repeat: no-repeat;
    background-position: right 7px center;
    padding-right: 26px;
}

.object-tools a.viewsitelink, .object-tools a.golink {
    background-image: url(../img/tooltag-arrowright.svg);
}

.object-tools a.addlink {
    background-image: url(../img/tooltag-add.svg);
}

/* OBJECT HISTORY */

table#change-history {
    width: 100%;
}

table#change-history tbody th {
    width: 16em;
}

/* PAGE STRUCTURE */

#container {
   	@include container();
}

#content {
	@include columns(12);
}

.dashboard #content {
   
}

#content-main {
	@include columns(9);
	&.full {
		@include columns(12);	
	}
}

#content-related {
	@include columns(3);
}

#footer {
    clear: both;
    padding: 10px;
}

/* COLUMN TYPES */

.colMS {
    margin-right: 300px;
}

.colSM {
    margin-left: 300px;
	#content-related {
	    float: left;
	    margin-right: 0;
	    margin-left: -300px;
	}
	#content-main {
    	float: right;
	}

}

.popup .colM {
    width: auto;
}

/* HEADER */

#header {
    width: auto;
    height: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 40px;
    background: darken($primaryColor, 10);
    color: #ffc;
    overflow: hidden;
	a{
		&:link, &:visited {
		    color: $textIconColor;
		}
		
		&:focus , &:hover {
		    text-decoration: underline;
		}
	}
}

#branding {
    float: left;
	h1 {
	    padding: 0;
	    margin: 0 20px 0 0;
	    font-weight: 300;
	    font-size: 24px;
	    color: #f5dd5d;
	
		a:link, a:visited {
		    color: #f5dd5d;
		}
	}
	
	
	h2 {
	    padding: 0 10px;
	    font-size: 14px;
	    margin: -8px 0 8px 0;
	    font-weight: normal;
	    color: #ffc;
	}
	
	a:hover {
	    text-decoration: none;
	}
}

#user-tools {
    float: right;
    padding: 0;
    margin: 0 0 0 20px;
    font-weight: 300;
    font-size: 11px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    text-align: right;
	a {
	    border-bottom: 1px solid rgba(255, 255, 255, 0.25);
	
		&:focus, &:hover {
		    text-decoration: none;
		    border-bottom-color: darken($textIconColor, 10);
		    color: darken($textIconColor, 10);
		}
	}
}

/* SIDEBAR */

#content-related {
	.module {
		h2 {
		    padding: 16px;
		    margin-bottom: 16px;
		    border-bottom: 1px solid #eaeaea;
		    font-size: 18px;
		    background: darken($primaryColor, 10);
		}
	}
	h3 {
	    font-size: 14px;
	    color: #666;
	    padding: 0 16px;
	    margin: 0 0 16px;
	}
	h4 {
	    font-size: 13px;
	}
	p {
	    padding-left: 16px;
	    padding-right: 16px;
	}
	.actionlist {
	    padding: 0;
	    margin: 16px;
		li {
		    line-height: 1.2;
		    margin-bottom: 10px;
		    padding-left: 18px;
		}
	}
}

.delete-confirmation form {
	input[type="submit"] {
	    background: #ba2121;
	    border-radius: 4px;
	    padding: 10px 15px;
	    color: #fff;
	
		&:active,
		&:focus,
		&:hover {
		    background: #a41515;
		}
	}
	.cancel-link {
	    display: inline-block;
	    vertical-align: middle;
	    height: 15px;
	    line-height: 15px;
	    background: #ddd;
	    border-radius: 4px;
	    padding: 10px 15px;
	    color: #333;
	    margin: 0 0 0 10px;

		&:active,
		&:focus,
		&:hover {
		    background: #ccc;
		}
	}
}

/* POPUP */
.popup {
	#content {
    	padding: 20px;
	}
	#container {
	    min-width: 0;
	}
	#header {
	    padding: 10px 20px;
	}
}